    function removeClass(e,pos){
      $(e).removeClass('p'+pos);
    }
    
    function removeClassv(e,pos){
      $(e).removeClass('v'+pos);
    }
    
    function addClass(e,pos){
      $(e).addClass('p'+pos);  
    }
    
    function addClassv(e,pos){
      $(e).addClass('v'+pos);
    }
    
    function setPosTermometro(e,pos){
        var posOld  =   getCorAtiva(e);
                     removeClass(e,posOld);
                     addClass(e,pos);
    }
    
    

